.button {
    color: white;
    padding: 5px 24px;
    height: 30px;
    border-radius: 3px;
    border: none;
}

.disabled {
    background: #004D99 !important;
    opacity: 0.5;
    color: grey;
}

.secondary {
    border: 1px solid #0066CC;
    background-color: white;
    color: #0066cc;
}

.default {
    background-color: #0059b3;
}

.red {
    background-color: red;
}

.default:hover {
    background: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background: #E5F0FA;
}


.default:active {
    background: #0059B3;
}

.secondary:active {
    background: #C9E5FF
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
}
